const fetchEventMetadata = async (baseUrl, orgUnitNames, eventIds, projectNames) => {
  const metadata = {
    programs: {},
    programStages: {},
    orgUnits: {},
    dataElements: {}
  };
  try {
    // Fetch DHIS2 programs by project names from CSV
    for (const projectName of [...new Set(projectNames)]) {
      const programUrl = `${baseUrl}/api/programs.json?filter=name:eq:${encodeURIComponent(projectName)}&fields=id,name,programStages[id]`;
      try {
        const response = await fetch(programUrl, {
          credentials: 'include'
        });
        const data = await response.json();
        if (data.programs && data.programs.length > 0) {
          const program = data.programs[0];
          metadata.programs[projectName] = program.id;
          console.log(`Found program: "${program.name}" (ID: ${program.id})`);
          if (program.programStages && program.programStages.length > 0) {
            metadata.programStages[projectName] = program.programStages[0].id;
          }
        } else {
          console.warn(`Program "${projectName}" not found`);
        }
      } catch (e) {
        console.error(`Error fetching program ${projectName}:`, e);
      }
    }

    // Fetch organization units by names from CSV 'program' field
    for (const orgUnitName of [...new Set(orgUnitNames)]) {
      const orgUnitUrl = `${baseUrl}/api/organisationUnits.json?filter=name:eq:${encodeURIComponent(orgUnitName)}&fields=id,name`;
      try {
        const response = await fetch(orgUnitUrl, {
          credentials: 'include'
        });
        const data = await response.json();
        if (data.organisationUnits && data.organisationUnits.length > 0) {
          const orgUnit = data.organisationUnits[0];
          metadata.orgUnits[orgUnitName] = orgUnit.id;
          console.log(`Found org unit: "${orgUnit.name}" (ID: ${orgUnit.id})`);
        } else {
          console.warn(`Organization unit "${orgUnitName}" not found`);
        }
      } catch (e) {
        console.error(`Error fetching org unit ${orgUnitName}:`, e);
      }
    }

    // Fetch existing events to get org units and other metadata
    for (const eventId of [...new Set(eventIds)]) {
      const eventUrl = `${baseUrl}/api/tracker/events/${eventId}.json`;
      try {
        const response = await fetch(eventUrl, {
          credentials: 'include'
        });
        const eventData = await response.json();
        if (eventData && eventData.orgUnit) {
          metadata.orgUnits[eventId] = eventData.orgUnit;
        }
        if (eventData && eventData.programStage) {
          metadata.programStages[eventId] = eventData.programStage;
        }
      } catch (e) {
        // Event doesn't exist, will be created as new event
        console.log(`Event ${eventId} doesn't exist, will create new event`);
      }
    }

    // Fetch data elements for the program stages found
    const programStageIds = [...new Set(Object.values(metadata.programStages))];
    for (const stageId of programStageIds) {
      if (stageId) {
        const stageUrl = `${baseUrl}/api/programStages/${stageId}.json?fields=programStageDataElements[dataElement[id,name]]`;
        try {
          const response = await fetch(stageUrl, {
            credentials: 'include'
          });
          const stageData = await response.json();
          if (stageData.programStageDataElements) {
            console.log(`Available data elements for program stage ${stageId}:`);
            stageData.programStageDataElements.forEach(psde => {
              const de = psde.dataElement;
              console.log(`- "${de.name}" (ID: ${de.id})`);
              const name = de.name.toLowerCase();

              // Map data elements that match our CSV fields
              if (name.includes('male') && !name.includes('female')) {
                metadata.dataElements.males = de.id;
              } else if (name.includes('female')) {
                metadata.dataElements.females = de.id;
              } else if (name.includes('total') && name.includes('person')) {
                metadata.dataElements.total = de.id;
              } else if (name.includes('project')) {
                metadata.dataElements.project = de.id;
              } else if (name.includes('district')) {
                metadata.dataElements.district = de.id;
              }
            });
            console.log(`Mapped data elements:`, metadata.dataElements);
          }
        } catch (e) {
          console.warn(`Could not fetch data elements for program stage ${stageId}:`, e);
        }
      }
    }
  } catch (error) {
    console.error('Error fetching metadata:', error);
  }
  return metadata;
};
const transformCSVToTrackerFormat = async (csvData, baseUrl) => {
  const lines = csvData.trim().split('\n');
  const headers = lines[0].split(',').map(h => h.replace(/"/g, '').trim());

  // Parse CSV to collect org unit names, project names, and event IDs
  const orgUnitNames = []; // CSV 'program' field
  const projectNames = []; // CSV 'project' field
  const eventIds = [];
  const rows = [];
  for (let i = 1; i < lines.length; i++) {
    const values = lines[i].split(',').map(v => v.replace(/"/g, '').trim());
    if (values.length !== headers.length) {
      continue;
    }
    const row = {};
    headers.forEach((header, index) => {
      row[header] = values[index];
    });
    rows.push(row);
    orgUnitNames.push(row.program); // "Test Test" - org unit
    projectNames.push(row.project); // "Workshop 2" - DHIS2 program
    eventIds.push(row.event);
  }

  // Fetch metadata for org units, programs and events
  const metadata = await fetchEventMetadata(baseUrl, orgUnitNames, eventIds, projectNames);
  const events = [];

  // Transform each row using fetched metadata
  for (const row of rows) {
    // Use specific program and org unit from CSV fields
    const programId = metadata.programs[row.project]; // "Workshop 2" -> DHIS2 program ID
    const programStageId = metadata.programStages[row.project]; // Program stage from "Workshop 2" program
    const orgUnitId = metadata.orgUnits[row.program]; // "Test Test" -> org unit ID

    // Skip if we don't have required metadata
    if (!programId || !programStageId || !orgUnitId) {
      console.warn(`Skipping event ${row.event}: Missing metadata - program: ${programId} (from "${row.project}"), programStage: ${programStageId}, orgUnit: ${orgUnitId} (from "${row.program}")`);
      continue;
    }

    // Transform to DHIS2 tracker event format
    const event = {
      event: row.event,
      status: row.status,
      occurredAt: row.occurredAt,
      createdAt: row.createdAt,
      updatedAt: row.updatedAt,
      program: programId,
      programStage: programStageId,
      orgUnit: orgUnitId,
      dataValues: []
    };

    // Add data values if we have matching data elements
    if (metadata.dataElements.males && row.Males) {
      event.dataValues.push({
        dataElement: metadata.dataElements.males,
        value: row.Males
      });
    }
    if (metadata.dataElements.females && row.Females) {
      event.dataValues.push({
        dataElement: metadata.dataElements.females,
        value: row.Females
      });
    }
    if (metadata.dataElements.total && row['Total Persons reached']) {
      event.dataValues.push({
        dataElement: metadata.dataElements.total,
        value: row['Total Persons reached']
      });
    }
    if (metadata.dataElements.project && row.project) {
      event.dataValues.push({
        dataElement: metadata.dataElements.project,
        value: row.project
      });
    }
    if (metadata.dataElements.district && row.District) {
      event.dataValues.push({
        dataElement: metadata.dataElements.district,
        value: row.District
      });
    }
    events.push(event);
  }
  return {
    events: events
  };
};
