// Function to convert period format from YYYYMM to "Month YYYY"
const formatPeriod = period => {
  if (!period || period.length !== 6) {
    return period;
  }
  const year = period.substring(0, 4);
  const month = period.substring(4, 6);
  const monthNames = ['January', 'February', 'March', 'April', 'May', 'June', 'July', 'August', 'September', 'October', 'November', 'December'];
  const monthIndex = parseInt(month, 10) - 1;
  if (monthIndex >= 0 && monthIndex < 12) {
    return `${monthNames[monthIndex]} ${year}`;
  }
  return period;
};

// Function to transform data values to match specified format
const transformDataValues = (dataValues, dataElementsLookup, orgUnitsLookup) => {
  if (!dataValues || !Array.isArray(dataValues)) {
    return [];
  }
  return dataValues.map(dataValue => {
    const dataElementName = dataElementsLookup[dataValue.dataElement] || dataValue.dataElement;
    const orgUnitName = orgUnitsLookup[dataValue.orgUnit] || dataValue.orgUnit;
    const formattedPeriod = formatPeriod(dataValue.period);
    return {
      dataElement: dataElementName,
      period: formattedPeriod,
      program: orgUnitName,
      value: dataValue.value,
      storedBy: dataValue.storedBy,
      lastUpdated: dataValue.lastUpdated
    };
  });
};

